<template>
  <div class="d-flex align-items-center justify-content-center h-100">
    <div class="block">
      <login-component v-if="isAuth" @change="change" />
      <registration-component v-else @change="change" />
    </div>
  </div>
</template>

<script>
import RegistrationComponent from "@/components/auth/RegistrationComponent.vue";
import LoginComponent from "../components/auth/LoginComponent.vue";

export default {
  components: {
    LoginComponent,
    RegistrationComponent,
  },
  data: () => ({
    isAuth: true,
  }),
  methods: {
    change(status = false) {
      this.isAuth = status;
    },
  },
};
</script>

